export default class Comic {
  constructor(id, titulo, personajes) {
    this.id = id;
    this.titulo = titulo;
    this.personajes = personajes;
  }

  static fromJson(json) {
    return new Comic(
        json.id,
        json.title,
        json.characters
    );
  }

  static interseccion([comics1, comics2]) {
    return comics1.filter(comic1 =>
        comics2.some(comic2 => comic1.equals(comic2)));
  }

  getId() {
    return this.id;
  }

  getTitulo() {
    return this.titulo;
  }

  getPersonajes() {
    return this.personajes;
  }

  equals(other) {
    return other instanceof Comic && other.id === this.id;
  }
}