import Personaje from '../model/personaje';
import Comic from "../model/comic";

export default {
  characters() {
    return fetch('/data/characters.json')
        .then(response => response.json())
        .then(json => json.map(Personaje.fromJson));
  },
  comics(characterId) {
    return fetch(`/data/comics-${characterId}.json`)
        .then(response => response.json())
        .then(json => json.map(Comic.fromJson));
  }
};
