const concat = (a, b) => a + b;

export default () => {
  const dom = {
    personaje1: document.querySelector('#personaje1'),
    personaje2: document.querySelector('#personaje2'),
    buscador: document.querySelector('#buscador'),
    botonBuscar: document.querySelector('#boton-buscar'),
    resultadosTBody: document.querySelector('#resultados tbody')
  };

  const addPersonajes = personajes => {
    let optionsHTML = personajes
        .map(personaje => `<option value="${personaje.getId()}">${personaje.getNombre()}</option>`)
        .reduce(concat, "<option>-- Selecciona un personaje --</option>");
    dom.personaje1.innerHTML = optionsHTML;
    dom.personaje2.innerHTML = optionsHTML;
  };

  const addComics = comics => dom.resultadosTBody.innerHTML = comics.map(comic => `
      <tr>
        <td>${comic.getId()}</td>
        <td>${comic.getTitulo()}</td>
        <td>${comic.getPersonajes()}</td>
      </tr>
    `).reduce(concat, "");

  const onBuscar = callback => {
    dom.botonBuscar.addEventListener('click', event => {
      event.preventDefault();
      event.stopPropagation();

      callback(
          dom.buscador.personaje1.value,
          dom.buscador.personaje2.value
      );
    });
  };

  return {addPersonajes, addComics, onBuscar};
}